* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: Lato, sans-serif;
  letter-spacing: .05rem;
}

.App {
  text-align: center;
  height: inherit;
}

.heading {
  padding: 2rem;
  font-size: 2rem;
  letter-spacing: .8rem;
  text-transform: uppercase;
  color: white;
  -webkit-animation: heading 1s;
          animation: heading 1s;
}

@-webkit-keyframes heading {
  from {
    -webkit-transform: translateX(-10rem);
            transform: translateX(-10rem);
    opacity: 0;
}

  80% {
    -webkit-transform: translateX(1rem);
            transform: translateX(1rem);
  }

to {
  -webkit-transform: translateX(0rem);
          transform: translateX(0rem);
    opacity: 1;
}
}

@keyframes heading {
  from {
    -webkit-transform: translateX(-10rem);
            transform: translateX(-10rem);
    opacity: 0;
}

  80% {
    -webkit-transform: translateX(1rem);
            transform: translateX(1rem);
  }

to {
  -webkit-transform: translateX(0rem);
          transform: translateX(0rem);
    opacity: 1;
}
}

#search {
  background-color: transparent;
  margin: 0 35% 2% 35%;
  text-align: center;
  padding: 1%;
  font-size: 1.2rem;
  width: 30%;
  height: 5%;
  border: none;
  border-bottom: 1.5px solid white;
  color: white;
  outline: none;
  -webkit-animation: search 1s;
          animation: search 1s;
}

@-webkit-keyframes search {
  from {
    -webkit-transform: translateX(10rem);
            transform: translateX(10rem);
    opacity: 0;
}

  80% {
    -webkit-transform: translateX(-1rem);
            transform: translateX(-1rem);
  }

to {
  -webkit-transform: translateX(0rem);
          transform: translateX(0rem);
    opacity: 1;
}
}

@keyframes search {
  from {
    -webkit-transform: translateX(10rem);
            transform: translateX(10rem);
    opacity: 0;
}

  80% {
    -webkit-transform: translateX(-1rem);
            transform: translateX(-1rem);
  }

to {
  -webkit-transform: translateX(0rem);
          transform: translateX(0rem);
    opacity: 1;
}
}

::-webkit-input-placeholder {
  color: white;
  font-style: italic;
}

:-ms-input-placeholder {
  color: white;
  font-style: italic;
}

::-ms-input-placeholder {
  color: white;
  font-style: italic;
}

::placeholder {
  color: white;
  font-style: italic;
}

@media only screen and (max-width: 600px) {
  #search {font-size: .7rem;}
} 



